$color-grey: #616161;
$color-grey-light: #C8C8C8;
$color-green: #2EE65F;
$color-orange: #F1A139;
$color-red: #F23A3A;
$color-black: #000000;


//Media Query manager

//0px      -  450px:       Small Phone
// 450px   -   600px:      Phone 
// 600px   -   900px:      Tablet Potrait
// 900px   -   1075px:     Tablet Landscape
// 1075px   -  1200px:     Small Desktop
// 1200px   -  1400px:     Medium Desktop
// 1400+                   Normal Styles


@mixin respond($breakpoint) {

    @if $breakpoint==small-phone {
        @media only screen and (max-width: 28.125em) {
            @content
        }

        ;
    }

    @if $breakpoint==phone {
        @media only screen and (max-width: 37.5em) {
            @content
        }

        ;
    }

    @if $breakpoint==tab-port {
        @media only screen and (max-width: 56.25em) {
            @content
        }

        ;
    }

    @if $breakpoint==tab-land {
        @media only screen and (max-width: 67.185em) {
            @content
        }

        ;
    }

    @if $breakpoint==small-desktop {
        @media only screen and (max-width: 75em) {
            @content
        }

        ;
    }

    @if $breakpoint==medium-desktop {
        @media only screen and (max-width: 87.5em) {
            @content
        }

        ;
    }

}